.list__item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 6px 3px;
  border-bottom: 1px solid var(--border-light);
}

.list__artist {
  font-size: 11px;
}

.part {
  max-width: calc(100% - 40px);
}

.link .explicit {
  padding-left: 10px;
}

.dark .list__item {
  border-bottom: 1px solid var(--border-dark);
}

.list__link--song {
  font-weight: 500;
  font-size: 17px;
}

.videos__container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.videos__list {
  margin: 10px;
  width: calc(100% / 3 - 20px);
}

.videos__list .desc {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.list__item .index,
.videos__list .index {
  text-align: center;
  min-width: 40px;
}

.videos__list a {
  max-width: 190px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}



